.App {
}

/*Header Navigation*/

.hamburger:focus {
    outline: none;
}

.nav-wrapper {
    color: #000;
    overflow: auto;
    padding-right:20px;
}
.anchor{
    display:block;
    position:relative;
    top:-80px;
    visibility: hidden;
}

.nav-list {
    position: absolute;
    background-color: #FFF;
    list-style: none;
    margin: 10px 0 0 0;
    padding: 0 20px 0 0;
    right: 0;
    -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
            box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
}
.nav-list li{
    margin: 20px 0px 20px 20px;
}

.nav-list a {
    color:inherit;
    text-decoration: none;
    font-family: 'Andale Mono';
    font-size:1.1em;
}

/*Header*/

.App-header {
  position: fixed;
  top:0;
  left:0;
  background-color: #FFF;
  height: 80px;
  width: 100%;
  padding: 0;
  color: white;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  z-index: 500;
}


.App-title {
  font-size: 1em;
  font-family: 'Andale Mono';
  display: inline-block;
  padding-left: 20px;
  color: #000;
}

/*Body*/

.App-body {
    margin-top: 80px;
    overflow: auto;
    background-color: #FFF;
    height:100%;
}

.divider {
    background-color: #1f3652;
    height: 50px;
}

.section {
    text-align: center;
    overflow: auto;
}

.section-header {
    font-size: 20px;
    font-family: 'Andale Mono';
    margin: 20px;
}
.section-body {
    margin: 10px;
}

.about {
    /*background-color: #d6dde3;*/
    /*background-color: #eeebdc;*/
    /*background-color: #1d2021;*/
    background-color: #c1c1bf;
    /*color: #FFF;*/
}
.projects {
    background-color: #c1c1bf;
}

.card {
    width: 85%;
    max-width: 800px;
    margin: 30px auto;
    /*background-color: #FFF;*/

    display: -ms-flexbox;

    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;

    overflow: hidden;
    height: 500px;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.card-header {
    padding: 5px;
    font-family: 'Andale Mono';
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex: 0 1 auto;
        flex: 0 1 auto;
}

.card-header a {
  color:inherit;
  text-decoration: none;
}

.card-header a:hover{
  color:white;
}

.header-title {
    margin: 5px;
    font-size: 32px;
    color: #000;
}

.card-media {
    /*background-color: #acc7dc;*/
    background-color: #504945;
    -ms-flex: 1 1 50px;
        flex: 1 1 50px;
}

.card-body {
    -ms-flex: 0 1 auto;
        flex: 0 1 auto;
    text-align: left;
    font-family: 'Andale Mono';
    font-size: 14px;
}

.card-text {
    margin: 20px;
}

p {
    margin: 0px;
}

/* shell css */
.shell-wrap {
  width: 700px;
  margin: 20px auto 50px auto;
  border-radius: 3px;
}

.shell-top-bar {
  text-align: center;
  color: #525252;
  padding: 5px 0;
  margin: 0;
  text-shadow: 1px 1px 0 rgba(255,255,255,0.5);
  font-size: 0.85em;

  -webkit-border-top-left-radius: 3px;
  -webkit-border-top-right-radius: 3px;
  -moz-border-radius-topleft: 3px;
  -moz-border-radius-topright: 3px;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;

  background: #E8E8EE;
}

.shell-body {
  margin: 0;
  padding: 5px;
  list-style: none;
  background: #282828;
  color: #928374;
  font: 0.8em 'Andale Mono', Consolas, 'Courier New';
  line-height: 1.6em;
  text-align: left;

  -webkit-border-bottom-right-radius: 3px;
  -webkit-border-bottom-left-radius: 3px;
  -moz-border-radius-bottomright: 3px;
  -moz-border-radius-bottomleft: 3px;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
}

.command:before {
  content: '>>>';
  position: absolute;
  left: 0;
  top: 0;
}

.command {
  word-wrap: break-word;
  position: relative;
  padding: 0 0 8px 30px;
}

.result {
  word-wrap: break-word;
  position: relative;
  padding: 0 0 0 15px;
}
.result a {
  color:inherit;
  text-decoration: none;
 }



